import React, { useState } from 'react'
import { Particles } from 'react-particles-js';

export default function Home() {

    const [mobile, setMobile] = useState(window.innerWidth < 600)
    const [dekstop, setDekstop] = useState(window.innerWidth > 700)

    React.useEffect(() => {
        window.addEventListener('resize', () => {setMobile(window.innerWidth < 600); setDekstop(window.innerWidth > 700)})
    })

    return (
        <div className="home" id="home">
            <div className="particle">
                <Particles
                    params={{
                    particles: {
                        "number": {
                            "value": 80,
                            "density": {
                            "enable": true,
                            "value_area": 700
                            }
                        },
                        "color": {
                            "value": "#ffffff"
                        },
                        "shape": {
                            "type": "circle",
                            "stroke": {
                            "width": 0,
                            "color": "#000000"
                            },
                            "polygon": {
                            "nb_sides": 5
                            },
                        },
                        "opacity": {
                            "value": 0.1,
                            "random": false,
                            "anim": {
                            "enable": false,
                            "speed": 0.1,
                            "opacity_min": 0.1,
                            "sync": false
                            }
                        },
                        "size": {
                            "value": 3,
                            "random": true,
                            "anim": {
                            "enable": false,
                            "speed": 10,
                            "size_min": 0.1,
                            "sync": false
                            }
                        },
                        "line_linked": {
                            "enable": true,
                            "distance": 150,
                            "color": "#ffffff",
                            "opacity": 0.1,
                            "width": 1
                        },
                        "move": {
                            "enable": true,
                            "speed": 2,
                            "direction": "none",
                            "random": false,
                            "straight": false,
                            "out_mode": "out",
                            "bounce": true,
                            "attract": {
                            "enable": false,
                            "rotateX": 600,
                            "rotateY": 1200
                            }
                        }
                        },
                        "interactivity": {
                        "detect_on": "canvas",
                        "events": {
                            "onhover": {
                            "enable": true,
                            "mode": "grab"
                            },
                            "onclick": {
                            "enable": true,
                            "mode": "push"
                            },
                            "resize": true
                        },
                        "modes": {
                            "grab": {
                            "distance": 200,
                            "line_linked": {
                                "opacity": 0.1
                            }
                            },
                        }
                    },
                    }}
                />
            </div>

            <div className="center">
                {/* <p className='text'>Hello, I'm <span>Scott Engelbrecht</span>.</p> */}
                <div className={`text ${mobile ? "mobile" : ""}`}>
                    Hello, Im <span>LxtteDev</span>.
                </div>
                <div className={`job${mobile ? " mobile" : ""}${dekstop ? " desktop" : ""}`}>
                    I'm a freelance programmer and on/off web developer.
                </div>
            </div>
        </div>
    )
}
