import React, { useState } from 'react'

import Typescript from "../Images/Logos/Typescript.png"
import Javascript from "../Images/Logos/Javascript.png"
import Lua from "../Images/Logos/Lua.png"

export default function About() {

    const [mobile, setMobile] = useState(window.innerWidth < 600)
    
    React.useEffect(() => {
        window.addEventListener('resize', () => {setMobile(window.innerWidth < 600)})
    })

    return (
        <div className='about' id="about">
            <div className='header'>
                <h2>About Me</h2>
                <div className="underline"></div>
            </div>
            <div className="programs-title">Languages I work with</div>
            <div className={`programs ${mobile ? "mobile" : ""}`}>
                <div className="program">
                    <img src={Typescript} alt="typescript" className='image' />
                    <p className="title">TypeScript</p>
                    {/* <p className="description">Description</p> */}
                    <a href='/home#projects' className="examples" onClick={() => {console.log("Clicked")}}>Show Examples</a>
                </div>
                <div className="program">
                    <img src={Javascript} alt="typescript" className='image' />
                    <p className="title">JavaScript</p>
                    {/* <p className="description">Description</p> */}
                    <a href='/home#projects' className="examples" onClick={() => {console.log("Clicked")}}>Show Examples</a>
                </div>
                <div className="program">
                    <img src={Lua} alt="typescript" className='image' />
                    <p className="title">Lua</p>
                    {/* <p className="description">Description</p> */}
                    <a href='/home#projects' className="examples" onClick={() => {console.log("Clicked")}}>Show Examples</a>
                </div>
                {/* <ul>
                    <li>
                        <img src="" alt="" />
                        <p>Typescript</p>
                    </li>
                    <li>
                        <img src="" alt="" />
                        <p>Javascript</p>
                    </li>
                    <li>
                        <img src="" alt="" />
                        <p>C#</p>
                    </li>
                </ul> */}

            </div>
        </div>
    )
}
