
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faTimes } from '@fortawesome/free-solid-svg-icons';

import React, { useState, useEffect, Component } from "react";

export default class Navbar extends Component {
    // ismobile state

    constructor(props) {
        super(props);
        this.state = {
            isMobile: window.innerWidth < 600,
            open: false
        }
    }

    render() {
        return (
            <div className='navbar'>
                <div className={`navbar-icon ${this.state.isMobile ? 'mobile' : '' }`}>
                    <FontAwesomeIcon className={`icon`} icon={this.state.open ? faTimes : faBars} enableBackground={false} onClick={() => {this.setState({open: !this.state.open})}}  />
                </div>

                <div className={`menu`}>
                    <div className={`content ${this.state.open ? "active" : "" }`}>
                        <ul>
                            <li><a href="/home#projects">Projects</a></li>
                            <li><a href="/home#about">About</a></li>
                            <li><a href="/home#home">Home</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount() {
        window.addEventListener('resize', () => {
            this.setState({
                isMobile: window.innerWidth < 600
            });
        }, false);
    }
}
