import React from 'react'
import { Redirect } from 'react-router-dom'

// Import images from Images/Backgrounds
import Background from "../Images/Backgrounds/Some Image.jpg"

export default function Projects() {

    // setup states
    const [state, setState] = React.useState({
        active: "All",
    })

    return (
        <div className='projects' id='projects'>
            <div className='header'>
                <h2>Projects</h2>
                <div className="underline"></div>
            </div>
            
            <div className="types">
                <div className={`type ${state.active === "All" ? "active" : ""}`} onClick={() => {setState({active: "All"})}}>
                    <p>All</p>
                </div>
                <div className={`type ${state.active === "TS" ? "active" : ""}`} onClick={() => {setState({active: "TS"})}}>
                    <p>TypeScript</p>
                </div>
                <div className={`type ${state.active === "JS" ? "active" : ""}`} onClick={() => {setState({active: "JS"})}}>
                    <p>JavaScript</p>
                </div>
                <div className={`type ${state.active === "LUA" ? "active" : ""}`} onClick={() => {setState({active: "LUA"})}}>
                    <p>Lua</p>
                </div>
            </div>

            <div className="container">

                {/* Javascript items */}
                <div className={`project js ${(state.active === "JS" || state.active === "All") ? '' : 'inactive'}`}>
                    <div className="content">
                        <p className="title">LxtteDev</p>
                        <p className="made">Made with <span>JavaScript</span></p>
                        <div className="select" >
                            <a href="/home#home">Veiw</a>
                        </div>
                    </div>
                </div>

                {/* Typescript Items */}

                {/* Lua Items */}

                {/* Mixed Items */}
                <div className={`project lua ts ${(state.active === "LUA" || state.active === "TS" || state.active === "All") ? '' : 'inactive'}`}>
                    <div className="content">
                        <p className="title">Haze Studios</p>
                        <p className="made">Made with <span>LUA</span> and <span>TypeScript</span></p>
                        <div className="select" >
                            <a href="https://www.roblox.com/groups/10029720/Haze-Studios" target="_blank">Veiw</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
