import React from 'react'

import { default as Words } from '../Components/Words.json'

function FindWords(Includes, Excludes, Spots) {
     let ValidWords = []
     const IncludesList = Includes.toString().split(',')
     const ExcludesList = Excludes.toString().split(',')
     const SpotsList = Spots.toString().split(',')
     const SearchingWords = Words //Words.toString().split(',')

     console.log(`Searcing for words with '${IncludesList}' (${typeof (IncludesList)}) excluding '${ExcludesList}' (${typeof (ExcludesList)}) in the spots '${SpotsList}' (${typeof (SpotsList)})`)


     for (const key in SearchingWords) {
          const Word = SearchingWords[key].toString().trim()

          let Contains = true
          for (let Character in IncludesList) {
               Character = IncludesList[Character].trim()
               if (Character) {
                    if (Contains) {
                         Contains = Word.includes(Character)
                         if (!Contains) {
                              break
                         }
                    } else {
                         break
                    }
               }
          }

          console.log(`${Word}\n`)

          if (Contains) {
               let NContains = true
               for (let Character in ExcludesList) {
                    Character = ExcludesList[Character].trim()
                    if (Character) {
                         if (NContains) {
                              NContains = !Word.includes(Character)
                              if (!NContains) {
                                   break
                              }
                         } else {
                              break
                         }
                    }
               }

               if (NContains) {
                    let ContainsSpot = true
                    for (let Character in Word) {
                         const Letter = Word[Character]
                         const Spot = (SpotsList[Character] || '0').trim()

                         if (Spot !== '0') {
                              if (ContainsSpot) {
                                   ContainsSpot = Letter.toLocaleLowerCase() === Spot.toLocaleLowerCase()
                                   if (!ContainsSpot) {
                                        break
                                   }
                              } else {
                                   break
                              }
                         }
                    }

                    if (ContainsSpot) {
                         // console.log(Word)
                         ValidWords.push(Word)
                    }
               }
          }
     }

     return ValidWords
}

export default function () {
     return (
          <div className='worlde' id="worlde">
               <div className="home">
                    <div className="area"><p className="label">Included letters: </p><textarea className="textarea" placeholder='Seperated by ,' id='includes'></textarea></div>
                    <div className="area"><p className="label">Excluded letters: </p><textarea className="textarea" placeholder='Seperated by ,' id='excludes'></textarea></div>
                    <div className="area"><p className="label">Set places (0 for unknown): </p><textarea className="textarea" placeholder='Seperated by ,' id='spots'></textarea></div>

                    <button className='confirm' onClick={() => {
                         // Get  words div element
                         const WordsDiv = document.getElementById('words')

                         // Get the values from the text boxes
                         const Includes = document.getElementById('includes').value
                         const Excludes = document.getElementById('excludes').value
                         const Spots = document.getElementById('spots').value

                         // Find the words
                         const Valid = FindWords(Includes, Excludes, Spots)

                         // Clear words div
                         WordsDiv.innerHTML = ''
                         let WordsString = ''

                         // Show words
                         for (let Word of Valid) {
                              WordsString += `<p class='word'>${Word.trim() || "Error"}</p>`
                         }

                         WordsDiv.innerHTML = WordsString
                    }}>Search</button>

                    <div className="words" id='words'>

                    </div>
               </div >
          </div>
     )
}
